<template>
  <div id="app">
    <div class="video-container">
      <video
        ref="ref-video"
        controls
        preload
        poster="https://c.s-microsoft.com/zh-tw/CMSImages/Surface_Where2buy_Hero_JL_V3.jpg?version=15923625-15a3-6fbd-a28c-0a0d89b960e3"
        controlsList="nodownload"
      >
        <source :src="videoSrc" type="video/mp4">
      </video>
      <div class="video-overlay" @click="$refs['ref-video'].click()">
        <p :style="{opacity: textOpacity}"> {{ name }} </p>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'app',
  data: function () {
    return {
      videoId: this.getParameterByName('id'),
      name: this.getParameterByName('name'),
      textOpacity: 0,
    }
  },
  computed: {
    videoSrc: function () {
      // return `https://s3-ap-northeast-1.amazonaws.com/microsoft-videos/${this.videoId}.mp4`
      return `https://s3-ap-northeast-1.amazonaws.com/microsoft-videos/${this.videoId}.mp4`
    },
  },
  mounted: function () {
    this.$refs['ref-video'].ontimeupdate = (event) => {
      console.log(this.$refs['ref-video'].currentTime)
      if (this.$refs['ref-video'].currentTime >= 10 && this.$refs['ref-video'].currentTime < 15) {
        this.textOpacity = 1
      } else if (this.$refs['ref-video'].currentTime >= 15) {
        this.textOpacity = 0
      } else {
        this.textOpacity = 0
      }
    }
  },
  methods: {
    getParameterByName: function (name, url) {
      if (!url) {
        url = window.location.href
      }
      name = name.replace(/[\[\]]/g, "\\$&")
      const regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)")
      const results = regex.exec(url)
      if (!results) {
        return null
      }
      if (!results[2]) {
        return ''
      }

      return decodeURIComponent(results[2].replace(/\+/g, " "))
    },
  },
}
</script>

<style>
.video-container {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 640px;
  height: 360px;
  transform: translate(-50%, -50%);
}

.video-container video {
  position: relative;
  top: 0;
  width: 100%;
  height: 100%;
}

.video-container .video-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 90%;
  z-index: 2147483647;
  opacity: 0.9;
  font-size: 59pt;
}

.video-container .video-overlay p {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-family: Didot, "Didot LT STD", "Hoefler Text", Garamond, "Times New Roman", serif;
  color: white;
  text-align: center;
  margin: 0;
  transition: opacity 2.5s ease-in-out;
}

@media (max-width : 467px) {
  .video-container {
    width: 100%;
    height: unset;
  }
}
</style>



